.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}

.task-item__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
}

.task-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1.5;
  margin: 0 1rem;
  color: white;
}

.task-item h3 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.task-item p {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.task-item__status__ongoing {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #432ff1;
  /* border: 1px solid white; */
  padding: 0.5rem;
  border-radius: 4px;
}

.task-item__status__onhold {
  font-size: 1rem;
  font-weight: bold;
  color: rgb(31, 31, 31);
  background-color: rgb(235, 232, 51);
  /* border: 1px solid white; */
  padding: 0.5rem;
  border-radius: 4px;
}

.task-item__status__delay {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: rgb(207, 15, 15);
  /* border: 1px solid white; */
  padding: 0.5rem;
  border-radius: 4px;
}

.task-item__status__done {
  font-size: 1rem;
  font-weight: bold;
  color: rgb(31, 31, 31);
  background-color:rgb(60, 192, 60);
  /* border: 1px solid white; */
  padding: 0.5rem;
  border-radius: 4px;
}

@media (min-width: 580px) {
  .task-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .task-item__description h2 {
    font-size: 1.25rem;
  }

  .task-item__status {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}