.new-task {
  background-color:#c3b4f3;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-task button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid rgb(31, 31, 31);
  background-color: white;
  font-weight: bold;
  color: rgb(31, 31, 31);
  border-radius: 12px;
  margin-right: 1rem;
}

.new-task button:hover,
.new-task button:active {
  background-color: #510674;
  border-color: #510674;
  color: white;
}

.new-task button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.new-task button.alternative:hover,
.new-task button.alternative:active {
  background-color: #ddb3f8;
}