@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.tasks-filter {
  color: white;
  padding: 0 1rem;
}

.tasks-filter__control {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 1rem 0;
}

.tasks-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.tasks-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}
.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
.task-date {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #2a2a2a;
  color: white;
  border-radius: 12px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.task-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.task-date__year {
  font-size: 0.75rem;
}

.task-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}
.task-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}

.task-item__description {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-flex-flow: column-reverse;
          flex-flow: column-reverse;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.task-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  -webkit-flex: 1.5 1;
          flex: 1.5 1;
  margin: 0 1rem;
  color: white;
}

.task-item h3 {
  color: #3a3a3a;
  font-size: 1rem;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.task-item p {
  color: #3a3a3a;
  font-size: 1rem;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.task-item__status__ongoing {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #432ff1;
  /* border: 1px solid white; */
  padding: 0.5rem;
  border-radius: 4px;
}

.task-item__status__onhold {
  font-size: 1rem;
  font-weight: bold;
  color: rgb(31, 31, 31);
  background-color: rgb(235, 232, 51);
  /* border: 1px solid white; */
  padding: 0.5rem;
  border-radius: 4px;
}

.task-item__status__delay {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: rgb(207, 15, 15);
  /* border: 1px solid white; */
  padding: 0.5rem;
  border-radius: 4px;
}

.task-item__status__done {
  font-size: 1rem;
  font-weight: bold;
  color: rgb(31, 31, 31);
  background-color:rgb(60, 192, 60);
  /* border: 1px solid white; */
  padding: 0.5rem;
  border-radius: 4px;
}

@media (min-width: 580px) {
  .task-item__description {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex: 1 1;
            flex: 1 1;
  }

  .task-item__description h2 {
    font-size: 1.25rem;
  }

  .task-item__status {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
.tasks-list {
  list-style: none;
  padding: 0;
}

.tasks-list__fallback {
  color: white;
  text-align: center;
}
.chart-bar {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}
.chart {
  padding: 1rem;
  border-radius: 12px;
  background-color: #f8dfff;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 10rem;
}
.tasks {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
.new-task {
  background-color:#c3b4f3;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-task button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid rgb(31, 31, 31);
  background-color: white;
  font-weight: bold;
  color: rgb(31, 31, 31);
  border-radius: 12px;
  margin-right: 1rem;
}

.new-task button:hover,
.new-task button:active {
  background-color: #510674;
  border-color: #510674;
  color: white;
}

.new-task button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.new-task button.alternative:hover,
.new-task button.alternative:active {
  background-color: #ddb3f8;
}
.new-task__controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-task__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-task__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-task__actions {
  text-align: right;
}

.new-task__control select {
  font: inherit;
  padding: 0.5rem 3rem;
  /* font-weight: bold; */
  border-radius: 6px;
  border: none;
}
.app {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 65rem;
  max-width: 95%;
  border-radius: 12px;
  padding: 1rem;
  margin: 2rem;
  background-color: white;
}

.app h1 {
text-align: center; 
color: rgb(31, 31, 31); 
font: inherit; 
font-weight: bold; 
padding: '5px'; 
margin: 5px; 
font-size: 30px;
}
